      padding: 0;
      width: 100%;
  }
  .grey-container {
      margin: 50px 10%;
      
      
  }
}
  
.nav-container {
    display: flex;
    justify-content: space-between;
    margin: 3em 7%;
}
.nav-container ul{
    display: flex;
    list-style-type: none;
}

.nav-container li {
    padding-left: 20px;
}
.nav-container a {
    text-decoration: none;
}
@media only screen 
  and (max-device-width: 700px)
  and (-webkit-min-device-pixel-ratio: 2) {
    .nav-container {
        margin: 2em 10%;
        margin-bottom: 0 !important;
    }
    #date {
        display: none !important;
    }
}
.edit-btn {
    display: flex;
    justify-content: flex-end;
}
.edit-btn p:nth-child(2) {
    margin-left: 20px;
}
.blogEdit-container form{
    display: flex;
flex-direction: column;
width: 100%;
margin-bottom: 2em;
}
.blogEdit-container .save-btn{
    display: flex;
    justify-content: flex-end;
    padding-top: 20px;
}

.save-btn button {
    height: 40px;
    width: 70px;
    font-size: 1em;
}
.blogEdit-container input {
    height: 40px;
}
.blogEdit-container textarea {
    height: 200px;
}
.blogEdit-container h1 {
    padding: .2em 0;
}
